@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Sora:wght@400;600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Almarai:wght@700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --font-sora: Sora;
  --font-source-sans-pro: "Source Sans Pro";
  --font-almarai: Almarai;

  /* font sizes */
  --font-size-lg-4: 18.4px;
  --font-size-4xl-2: 23.2px;

  /* Colors */
  --color-black: #000;
  --color-white: #fff;
  --color-tomato: #fe5d41;
  --color-darkgray-100: #a4a4a4;

  /* border radiuses */
  --br-4xl: 23px;
  --br-114xl-3: 133.3px;
}
